
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

.container {
  background-color: #48657C;
}
.backdrop {
  background-color: #48657C;
}

.feedback-card {
  margin: auto;
  border-radius: 6px;
  border: 1.5px solid;
  border-color: #365468;
  background-color: #37586C;
  color: white;
  padding: 12px;
}
.feedback-card a {
  color: #56D4C7;
}
.input-group {
  padding: 12px 0 12px 0;
  color: white;
}
.input-group i {
  padding: 0 12px 0 12px;
}

.settings {
  background-color: #37586C;
  color: white;
  border: 1px solid;
  border-color: #37586C;
  border-radius: 12px;
}
.table th {
  border-top: None;
}
.table thead th {
  border-bottom: None;
}
.table td {
  border-top: 0.1px solid rgba(255,255,255,.25);
}

.select {
  position: relative;
  display: inline-block;
  width: 100%;
  min-width: 120px;
  font-size: 14px;
}
.table td, .table th {
  vertical-align: middle !important;
}
.select select {
  font-family: 'Arial';
  display: inline-block;
  width: 100%;
  cursor: pointer;
  padding: 6px 12px;
  outline: 0;
  border: 0px solid #000000;
  border-radius: 6px;
  background: #E6E6E6;
  color: black;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
}
.select select::-ms-expand {
  display: none;
}
.select select:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.select_arrow {
  position: absolute;
  top: 14px;
  right: 15px;
  width: 0;
  height: 0;
  color: black;
  pointer-events: none;
  border-style: solid;
  border-width: 6px 4px 0px 4px;
  border-color: #000 transparent transparent transparent;
}
.select select:hover~.select_arrow,
.select select:focus~.select_arrow {
  border-top-color: #000000;
}
.select select:disabled~.select_arrow {
  border-top-color: #CCCCCC;
}

.info {
  color: #BAC1C6;
  background-color: #2C343E;
  border: 1px solid;
  border-color: #2C343E;
  border-top-right-radius: 6px;
  border-top-left-radius: 6px;
  padding: 12px;
}

.toasted-container {
  width: 80%;
}
.toasted {
  padding: 12px;
}
.toasted.primary {
  min-height: 48px;
  border-radius: 6px;
}

.table.recent-table td {
  width: 100px;
  text-align: center;
  border-top: None;
}
textarea {
  width: 100%;
  border-radius: 6px;
  border: none;
  padding: 12px;
}
button {
  color: white;
  background-color: #52E0D1;
  border-color: #52E0D1;
  border-radius: 6px;
  margin: 3px;
  padding: 6px 12px 6px 12px;
  cursor: pointer;
}
button i {
  color: #FBD751;
  padding: 0 6px 0 0;
}
.headings {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 0;
}
.show-all {
  text-align: center;
  color: #52E0D1;
  font-size: 18px;
}
.recent {
  color: white;
}
.good-mood {
  color:  #A5D4DC;
}
.okay-mood {
  color: #FBD751;
}
.bad-mood {
  color: #F09F5A;
}
.disabled-mood {
  opacity: 0.3;
}
.activeLevelFull {
  color: red;
}
.activeLevelHalf {
  color: pink;
}
.activeLevelNone {
  color: purple;
}
.treatmentSelected {
  display: inline-block;
  color: white;
  background-color: #37586C;
  border-radius: 6px;
  margin: 3px;
  padding: 6px 12px 6px 12px;
}
.highlightTreatment {
  display: inline-block;
  background-color: #37586C;
  border: 1px solid #37586C;
  color: white;
  border-radius: 6px;
  margin: 3px;
  padding: 6px 12px 6px 12px;
}
.removeTreatment {
  display: inline-block;
  color: white;
  border: 1px solid white;
  border-radius: 6px;
  margin: 3px;
  padding: 6px 12px 6px 12px;
}
.logged-card {
  background-color: #323C4A;
  margin: 0 0 12px 0;
  padding: 12px;
  border: 1px solid #585F66;
  border-radius: 6px;
  cursor: pointer;
}
.unlogged-card {
  background-color: #3A4A5A !important;
  color: #BAC1C6;
  padding: 16px 24px;
}
.logged-full {
  background-color: #A5D4DC;
  padding: 3px;
  border-radius: 6px;
  margin: 6px;
}
.logged-half {
  background-color: #FBD751;
  border-radius: 6px;
  padding: 3px;
  margin: 6px;
}
.logged-missed {
  background-color: #F09F5A;
  border-radius: 6px;
  padding: 3px;
  margin: 6px;
}
.activity-level-div {
  text-align: center;
}
.ask-how {
  font-size: 20px;
  font-weight: bold;
  text-align: center;
}

ul {
  -webkit-padding-start: 0 !important;
}
.tabs-component-tabs {
  border: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.tabs-component-tab:hover {
  color: #666;
}
.tabs-component-tab.is-active {
  color: #000;
}
.tabs-component-tab.is-disabled *, .tabs-component-tab.is-disabled *:hover{
  color: rgba(255, 255, 255, 0.4);
  cursor: not-allowed !important;
}
.tabs-component-tab {
  list-style: none;
  width: 33%;
  background-color: #48657C;
  color: #FFF;
  border-radius: 3px 3px 0 0;
  text-align: center;
}
.tabs-component-tab.is-active {
  border-bottom: solid 2px #fff;
  color: #FFF;
  font-weight: bold;
}
.tabs-component-tab-a {
  color: white;
  display: block;
  padding: 12px;
  text-decoration: none;
}
.tabs-component-tab-a:hover {
  color: white;
  text-decoration: none;
}
.tabs-component-panels {
  padding: 12px;
}
